<template>
  <div class="page-container">
    <div class="blocks">
      <router-link
        class="block"
        to="/klanten"
      >
        <div class="block-title">
          <h2>
            <base-icon
              iconRef="people"
              position="left"
            />Klanten
          </h2>
        </div>
        <div class="block-content">Klantgegevens, projecten, ruimtes en metingen.</div>
        <div class="block-action">Bekijk
          <base-icon
            iconRef="arrow-forward"
            position="right"
          />
        </div>
      </router-link>
      <router-link
        class="block"
        to="/producten"
      >
        <div class="block-title">
          <h2>
            <base-icon
              iconRef="grid"
              position="left"
            />Producten
          </h2>
        </div>
        <div class="block-content">Productgegevens.</div>
        <div class="block-action">Bekijk
          <base-icon
            iconRef="arrow-forward"
            position="right"
          />
        </div>
      </router-link>
      <router-link
        class="block"
        to="/admin"
        v-if="adminModeOn"
      >
        <div class="block-title">
          <h2>
            <base-icon
              iconRef="flash"
              position="left"
            />Admin
          </h2>
        </div>
        <div class="block-content">Adminpaneel, gegevens, statistieken</div>
        <div class="block-action">Bekijk
          <base-icon
            iconRef="arrow-forward"
            position="right"
          />
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data: function() {
    return {};
  },
  computed: {
    adminModeOn() {
      return this.$store.getters.getAdminModeStatus;
    }
  },
  created: function() {
    this.$setPageTitle("Home");
  }
};
</script>

<style>
</style>



